body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center; }

.spin {
  text-align: center;
  background: rgba(0, 0, 0, 0.05);
  border-radius: 4px;
  margin-bottom: 20px;
  padding: 30px 50px;
  margin: 20px 0; }

.header {
  padding-bottom: 20; }

.content {
  padding-top: 20; }

.footer {
  padding-top: 500; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

*,
*:before,
*:after {
  box-sizing: border-box; }

body {
  margin: 20px;
  padding: 0;
  font-family: sans-serif;
  background: #f4f4f4; }

.clear {
  clear: both;
  content: ""; }

.game {
  margin: 0 auto;
  padding: 20px;
  background: #ececec; }
  .game .game-info {
    margin-bottom: 20px;
    background: #19a0d9;
    padding: 7px;
    text-align: center;
    color: #fff;
    min-height: 100px;
    border-radius: 7px; }
    .game .game-info .info {
      display: block;
      margin-top: 15px; }
  .game .cell {
    background: #7b7b7b;
    border: 1px solid #fff;
    float: left;
    line-height: 25px;
    height: 25px;
    text-align: center;
    width: 25px;
    cursor: pointer;
    border-radius: 3px;
    color: #fff;
    font-weight: 600; }
    .game .cell:focus {
      outline: none; }
  .game .hidden {
    background: #2e2829; }
  .game .is-flag,
  .game .is-mine {
    color: #fc543c; }

