@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

*,
*:before,
*:after {
  box-sizing: border-box;
}
body {
  margin: 20px;
  padding: 0;
  font-family: sans-serif;
  background: #f4f4f4;
}

.clear {
  clear: both;
  content: "";
}

.game {
  margin: 0 auto;
  padding: 20px;
  background: rgb(236, 236, 236);
  .game-info {
    margin-bottom: 20px;
    background: #19a0d9;
    padding: 7px;
    text-align: center;
    color: #fff;
    min-height: 100px;
    border-radius: 7px;
    .info {
      display: block;
      margin-top: 15px;
    }
  }

  .board {
  }

  .cell {
    background: #7b7b7b;
    border: 1px solid #fff;
    float: left;
    line-height: 25px;
    height: 25px;
    text-align: center;
    width: 25px;
    cursor: pointer;
    border-radius: 3px;
    color: #fff;
    font-weight: 600;

    &:focus {
      outline: none;
    }
  }

  .hidden {
    background: #2e2829;
  }

  .is-flag,
  .is-mine {
    color: #fc543c;
  }
}

